import { Box } from '@chakra-ui/react'
import React, { useContext, useEffect } from 'react'
import { useTable, usePagination, useSortBy, useGlobalFilter } from 'react-table'
import { merchatContext } from './useContext/createContext'
import { FaPen, FaTrashAlt, FaTags, FaCaretSquareDown } from 'react-icons/fa'
import { StyledPaginationButton as StyledButton } from 'utils/StyledButton'
import { RiMoneyDollarBoxFill } from 'react-icons/ri'
import ReusedModal from './components/ReusedModal'

export default function TableContent() {
	const { searchValue, setEditState } = useContext(merchatContext)

	useEffect(() => {
		setGlobalFilter(searchValue)
	}, [searchValue])
	const data = React.useMemo(
		() => [
			{
				id: 'OPMM001',
				name: '韓式短版上衣',
				position: '西門二店',
				condition: '在庫',
				type: '寄賣',
				purchasePrice: '1800',
				sellPrice: '1500',
			},
			{
				id: 'OPMM001',
				name: '韓式短版上衣',
				position: '西門二店',
				condition: '在庫',
				type: '寄賣',
				purchasePrice: '1800',
				sellPrice: '1500',
			},
			{
				id: 'OPMM001',
				name: '韓式短版上衣',
				position: '西門二店',
				condition: '在庫',
				type: '寄賣',
				purchasePrice: '1800',
				sellPrice: '1500',
			},
			{
				id: 'OPMM001',
				name: '韓式短版上衣',
				position: '西門二店',
				condition: '在庫',
				type: '寄賣',
				purchasePrice: '1800',
				sellPrice: '1500',
			},
			{
				id: 'OPMM001',
				name: '韓式短版上衣',
				position: '西門二店',
				condition: '在庫',
				type: '寄賣',
				purchasePrice: '1800',
				sellPrice: '1500',
			},
		],
		[]
	)

	const columns = React.useMemo(
		() => [
			{
				Header: '商品編號',
				accessor: 'id',
				Cell: (row) => {
					return row.value
				},
			},
			{
				Header: '品名',
				accessor: 'name',
				Cell: (row) => {
					return row.value
				},
			},
			{
				Header: '位置',
				accessor: 'position',
				disableGlobalFilter: true,
			},

			{
				Header: '商品狀態',
				accessor: 'condition',
				disableGlobalFilter: true,
			},
			{
				Header: '商品來源',
				accessor: 'type',
				disableGlobalFilter: true,
			},
			{
				Header: '進價',
				accessor: 'purchasePrice',
				disableGlobalFilter: true,
			},
			{
				Header: '售價',
				accessor: 'sellPrice',
				disableGlobalFilter: true,
			},
			{
				Header: '內容管理',
				accessor: 'management',
				disableGlobalFilter: true,
				Cell: (row) => {
					return (
						<Box d="flex" justifyContent="space-around">
							<ReusedModal type="定價" />
							<ReusedModal type="標籤" />
							<Box color="#91B0EB" cursor="pointer" onClick={() => setEditState((pre) => (pre = { isEdit: true, staffNumber: row.row.original.id }))}>
								<FaPen />
							</Box>
							<ReusedModal type="保留" />
							<ReusedModal type="刪除" />
						</Box>
					)
				},
			},
		],
		[]
	)

	const {
		getTableProps,
		getTableBodyProps,
		headerGroups,
		page,
		prepareRow,
		setGlobalFilter,
		pagination,
		canPreviousPage,
		canNextPage,
		pageOptions,
		pageCount,
		gotoPage,
		nextPage,
		previousPage,
		state: { pageIndex },
	} = useTable(
		{
			columns,
			data,
			initialState: { pageIndex: 0, pageSize: 10 },
		},
		useGlobalFilter,
		useSortBy,
		usePagination
	)
	return (
		<>
			<table {...getTableProps()}>
				<thead>
					{headerGroups.map((headerGroup, g) => (
						<tr {...headerGroup.getHeaderGroupProps()} key={g}>
							{headerGroup.headers.map((column, k) => (
								<th key={k}>
									<Box {...column.getHeaderProps(column.getSortByToggleProps())}>
										{
											// Render the header
											column.render('Header')
										}
									</Box>
								</th>
							))}
						</tr>
					))}
				</thead>
				<tbody {...getTableBodyProps()}>
					{
						// Loop over the table rows
						page.map((row, i) => {
							// Prepare the row for display
							prepareRow(row)
							return (
								// Apply the row props
								<tr {...row.getRowProps()} key={i}>
									{
										// Loop over the rows cells
										row.cells.map((cell, j) => {
											// console.log(cell.column.Header)
											// Apply the cell props
											return (
												<td {...cell.getCellProps()} key={j}>
													{
														// Render the cell contents
														cell.render('Cell')
													}
												</td>
											)
										})
									}
								</tr>
							)
						})
					}
				</tbody>
			</table>
			<Box d="flex" justifyContent="center" alignItems="center" pt="1em">
				<StyledButton onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
					{'<<'}
				</StyledButton>
				<StyledButton onClick={() => previousPage()} disabled={!canPreviousPage}>
					{'<'}
				</StyledButton>
				<Box color="#2250a6" fontSize="1.3em" mx=".5em">
					{pageIndex + 1}
				</Box>
				<StyledButton onClick={() => nextPage()} disabled={!canNextPage}>
					{'>'}
				</StyledButton>
				<StyledButton onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
					{'>>'}
				</StyledButton>
			</Box>
		</>
	)
}
