import Axios from './interceptors'

export default function UserService() {
	this.url = process.env.REACT_APP_API_URL
	this.login = async function () {
		let successResult
		let failedResult
		try {
			successResult = await Axios.post(this.url + '/login')
			// failedResult = await Axios.post(this.url + '/login-failed')
		} catch (error) {
			successResult = error.response || { status: 500 }
			failedResult = error.response || { status: 500 }
		}

		return successResult
	}
}
