import {
	Accordion,
	AccordionItem,
	AccordionButton,
	AccordionPanel,
	AccordionIcon,
	Box,
	Checkbox,
	Button,
	Input,
} from '@chakra-ui/react'
import { permissionList } from './permissionList'
import styled, { keyframes } from 'styled-components'
import { useForm } from 'react-hook-form'

const fadeInRight = keyframes`
  0% {
    transform:translate(100%)
  }
  100% {
	transform:translate(0%)
  }
`

const StyledBg = styled(Box)`
	width: 100%;
	display: flex;
	padding: 2em 2em 0;
	border-radius: 4px;
	background-color: #f9f9f9;
	flex-wrap: wrap;
`

const StyledForm = styled.form`
	width: 100%;
	animation: ${fadeInRight} 0.5s ease;

	input[type='checkbox'] {
		border: 1px solid;
		padding: 5px;
		appearance: none;
		outline: none;
	}

	input[type='checkbox']:checked {
		appearance: auto;
		background: none !important;
		color: black !important;
	}
`

//TODO: 把所有選項都變成component

export default function SubContent({ setEditStatus }) {
	const { register, handleSubmit } = useForm()
	return (
		<StyledForm onSubmit={handleSubmit((e) => console.log(e))}>
			<StyledBg>
				{permissionList.map((items) => (
					<Box w="33%" minW="290px" mb="1em">
						<Box fontSize="1.43em" color="#8F8F8F">
							{items.name}
						</Box>
						<Accordion allowToggle>
							{items.permission_type.map((item) => (
								<AccordionItem
									border="none"
									d="flex"
									alignItems="center"
									flexWrap="wrap"
								>
									<input type="checkbox" />
									<AccordionButton
										flexShrink="0"
										w="100px"
										p="0.5em 0"
										_active={{ outline: 'none', boxShadow: 'none' }}
										_focus={{ outline: 'none', boxShadow: 'none' }}
									>
										<Box ml=".5em" textAlign="left" isTruncated>
											{item.name}
										</Box>
										<AccordionIcon />
									</AccordionButton>

									<AccordionPanel pb={4} w="100%" d="flex" flexWrap="wrap">
										{item.permission.map((el) => (
											<Box d="flex" alignItems="center" mr="1em">
												<input
													type="checkbox"
													id="vehicle1"
													name="vehicle1"
													{...register(el.name)}
												/>
												<Box ml=".5em">{el.name}</Box>
											</Box>
										))}
									</AccordionPanel>
								</AccordionItem>
							))}
						</Accordion>
					</Box>
				))}
			</StyledBg>
			<Box textAlign="center" py="1em">
				<Button mx=".5em" onClick={() => setEditStatus((pre) => false)}>
					取消
				</Button>
				<Button mx=".5em" type="submit">
					儲存
				</Button>
			</Box>
		</StyledForm>
	)
}
