import React, { useContext } from 'react'
import { Box, Text, Input, useDisclosure } from '@chakra-ui/react'
import styled from 'styled-components'
import { HiPlusSm } from 'react-icons/hi'
import { purchaseContext } from './useContext/createContext'
import { StyledButtonBox } from 'utils/StyledButton'
import { MdKeyboardArrowRight, MdPrint } from 'react-icons/md'
import PrintTableModal from './component/PrintTableModal'
const StyledTopbar = styled(Box)`
	display: flex;
	justify-content: space-between;
	background: #f9f9f9;
	padding: 0.5em 1em;
	border-radius: 5px;
	align-items: center;
	margin-bottom: 1em;
	position: relative;
	overflow: hidden;

	&::before {
		content: '';
		position: absolute;
		left: 0%;
		width: 1px;
		height: 100%;
		border-left: 3px solid #b6d3ff;
		border-radius: 20 px;
	}
`

export default function TableTopBar() {
	const { setSearchValue, setEditState, editState } =
		useContext(purchaseContext)
	const { isOpen, onOpen, onClose } = useDisclosure()
	return (
		<StyledTopbar>
			<Box fontSize="1.25em" fontWeight="700" d="flex">
				進貨管理
				{editState.step !== 0 ? (
					<Box d="flex" alignItems="center">
						<Box mx=".5em">
							<MdKeyboardArrowRight size="1.5em" />
						</Box>
						新增進貨單
					</Box>
				) : null}
			</Box>
			<Box d="flex">
				{editState.step === 0 || editState.step === 1 ? (
					<>
						<Input
							mr="1em"
							maxW="160px"
							onChange={(e) => {
								setSearchValue(e.target.value)
							}}
							placeholder="請輸入員編或姓名"
						/>

						<StyledButtonBox
							onClick={() => setEditState((pre) => (pre = { ...pre, step: 1 }))}
						>
							<HiPlusSm />
							<Text ml="5px">新增進貨單</Text>
						</StyledButtonBox>
					</>
				) : null}
				{editState.step === 2 ? (
					<>
						<StyledButtonBox onClick={() => window.print()}>
							<MdPrint /> 列印
						</StyledButtonBox>
						<PrintTableModal isOpen={isOpen} onClose={onClose} />
					</>
				) : null}
			</Box>
		</StyledTopbar>
	)
}
