import { createSlice } from '@reduxjs/toolkit'

let initialState = [
	{
		title: '人事管理',
		isToggle: false,
		base: 'hr',
		children: [
			{
				title: '用戶管理',
				hashRoute: 'staff',
				data: '',
			},
			{
				title: '出勤管理',
				hashRoute: 'attendance',
				data: '',
			},
		],
	},
	{
		title: '進貨管理',
		isToggle: false,
		base: 'purchase',
		children: [{ title: '進貨單', hashRoute: 'purchaseSheet', data: '' }],
	},
	{
		title: '庫存管理',
		isToggle: false,
		base: 'stock',
		children: [
			{ title: '商品管理', hashRoute: 'merchant', data: 'merchant' },
			{ title: '調撥管理', hashRoute: 'transfer', data: 'transfer' },
		],
	},
	{ title: '廠商管理', isToggle: false, children: [{ title: '廠商管理' }] },
	{
		title: '銷貨管理',
		isToggle: false,
		children: [{ title: '銷貨單' }],
	},
	{
		title: '付款管理',
		isToggle: false,
		children: [{ title: '付款單' }],
	},
	{
		title: '報表管理',
		isToggle: false,
		children: [{ title: '銷貨報表' }, { title: '付款報表' }],
	},
	{
		title: '退貨管理',
		isToggle: false,
		children: [{ title: '寄貨退貨' }, { title: '銷貨退貨' }],
	},
	{
		title: '其他管理',
		isToggle: false,
		base: 'other',
		children: [
			{
				title: '權限設定',
				hashRoute: 'auth',
			},
			{
				title: '類別設定',
				hashRoute: 'category',
			},
			{
				title: '尺寸設定',
				hashRoute: 'size',
			},
			{
				title: '顏色設定',
				hashRoute: 'color',
			},
			{
				title: '狀況設定',
				hashRoute: 'condition',
			},
			{
				title: '品牌設定',
				hashRoute: 'brand',
			},
			{
				title: '位置設定',
				hashRoute: 'position',
			},
		],
	},
]

export const sidebarSlice = createSlice({
	name: 'sidebar',
	initialState: initialState,
	reducers: {
		toggle: (state, action) => {
			state.map((item) => (item.title === action.payload ? (item.isToggle = true) : (item.isToggle = false)))
		},
		setRoute: (state, action) => {
			console.log(action)
			state.forEach((items) => {
				items.children.forEach((item) => {
					if (action.payload === item.title) {
						console.log('admin/' + items.base + '/' + item.hashRoute)
						window.location.hash = 'admin/' + items.base + '/' + item.hashRoute
					}
				})
			})
		},
	},
})

export const { toggle, setRoute } = sidebarSlice.actions

export default sidebarSlice.reducer
