import React from 'react'
import {
	Modal,
	ModalOverlay,
	ModalContent,
	ModalHeader,
	ModalFooter,
	ModalBody,
	Box,
	ModalCloseButton,
} from '@chakra-ui/react'
import DisplayTable from './DisplayTable'
import { useEffect } from 'react'

export default function PrintTableModal({ isOpen, onClose }) {
	return (
		<Modal isOpen={isOpen} onClose={onClose}>
			<ModalOverlay />
			<ModalContent>
				<DisplayTable />
			</ModalContent>
		</Modal>
	)
}
