import { Box, Input, FormLabel, Text, Select } from '@chakra-ui/react'
import { useState } from 'react'
import { TiCamera } from 'react-icons/ti'
import styled, { keyframes } from 'styled-components'
import { FiChevronDown } from 'react-icons/fi'
import { BiNoEntry } from 'react-icons/bi'
import axios from 'axios'

const fadeInRight = keyframes`
  0% {
    transform:translate(100%)
  }
  100% {
	transform:translate(0%)
  }
`

const StyledBg = styled(Box)`
	width: 100%;
	display: flex;
	padding: 0.7em 0.5em;
	border-radius: 10px;
	background-color: #f9f9f9;
	flex-wrap: wrap;
	justify-content: space-between;
	margin: 0.7em 0;
`

const StyledInput = styled(Input)`
	width: 180px !important;
	margin-bottom: 2.8em;
`

const GridBox = styled(Box)`
	width: 100%;
	display: grid;

	grid-template-columns: repeat(4, 25%);

	.photoBox {
		grid-column-start: 4;
		grid-row-start: 2;
		grid-row-end: 4;
	}
	.noteBox {
		grid-column-start: 2;
		grid-column-end: 4;
		grid-row-start: 3;
	}
`

export default function SingleItem({
	register,
	order,
	setMerchantListData,
	merchantListData,
}) {
	const [toggleState, setToggleState] = useState(false)
	const updateData = (name, value) => {
		let array = merchantListData.map((item, i) => {
			if (i === order) {
				return { ...item, [name]: value }
			} else {
				return item
			}
		})
		setMerchantListData(array)
	}

	const fileHandler = (file) => {
		const formData = new FormData()

		formData.append('image', file)
		axios
			.post('http://localhost:4000/image', formData)
			.then((res) => console.log(res))
		// let fileInstance = URL.createObjectURL(file)
		// updateData('merchantImage', fileInstance)
		// console.log(fileInstance)
	}

	return (
		<>
			<StyledBg
				position="relative"
				maxH={
					merchantListData.length - 1 > order && !toggleState ? '0' : '2000px'
				}
				transition="all .5s ease"
				minH="3em"
				overflow="hidden"
			>
				<Box
					d={merchantListData.length - 1 > order ? 'flex' : 'none'}
					justifyContent="space-between"
					alignItems="center"
					position="absolute"
					bgColor="gray.600"
					left="0"
					right="0"
					top="0"
					minH="3em"
					borderRadius="10px"
					px=".5em"
					onClick={() => {
						setToggleState((pre) => !pre)
					}}
					cursor="pointer"
					_hover={{ bgColor: 'gray.700' }}
					transition="all .2s ease"
				>
					<Box color="white" d="flex" alignItems="center">
						<Box color="red">
							<BiNoEntry />
						</Box>
						<Box mx="1em">01</Box>
						<Box mr="3em">1100101-001</Box>
						<Box>台式台客短褲</Box>
					</Box>
					<Box color="white">
						<FiChevronDown />
					</Box>
				</Box>
				<Box
					d="flex"
					w="100%"
					justifyContent="space-between"
					flexWrap="wrap"
					mt={merchantListData.length - 1 > order ? '3em' : null}
					padding="3px"
				>
					<GridBox>
						<Box>
							<FormLabel d="flex">
								商品編號<Text color="red">*</Text>
							</FormLabel>
							<StyledInput
								placeholder="1100101-001"
								name="merchantID"
								onChange={(e) => {
									updateData(e.target.name, e.target.value)
								}}
							/>
						</Box>
						<Box>
							<FormLabel d="flex">
								品名<Text color="red">*</Text>
							</FormLabel>
							<StyledInput
								placeholder="1100101-001"
								name="merchantName"
								onChange={(e) => {
									updateData(e.target.name, e.target.value)
								}}
							/>
						</Box>
						<Box>
							<FormLabel d="flex">
								類別<Text color="red">*</Text>
							</FormLabel>
							<Box d="flex" w="180px">
								<Select mr="1em">
									<option value="" disabled selected>
										主類別
									</option>
									<option value="123">123</option>
								</Select>
								<Select>
									<option value="" disabled selected>
										子類別
									</option>
									<option value=""></option>
								</Select>
							</Box>
						</Box>
						<Box>
							<FormLabel d="flex">
								品牌名<Text color="red">*</Text>
							</FormLabel>
							<StyledInput placeholder="1100101-001" />
						</Box>
						<Box>
							<FormLabel d="flex">
								顏色<Text color="red">*</Text>
							</FormLabel>
							<StyledInput placeholder="1100101-001" />
						</Box>
						<Box>
							<FormLabel d="flex">
								尺寸<Text color="red">*</Text>
							</FormLabel>
							<StyledInput placeholder="1100101-001" />
						</Box>
						<Box>
							<FormLabel d="flex">
								狀況<Text color="red">*</Text>
							</FormLabel>
							<StyledInput placeholder="1100101-001" />
						</Box>
						<Box className="photoBox">
							<FormLabel d="flex">
								拍照<Text color="red">*</Text>
							</FormLabel>
							<Box
								border="2px dashed"
								borderColor="gray.600"
								_hover={{ borderColor: 'gray.700' }}
								w="180px"
								h="180px"
								borderRadius="15px"
								position="relative"
							>
								<Box
									position="absolute"
									w="100%"
									h="100%"
									d="flex"
									justifyContent="center"
									alignItems="center"
									flexDirection="column"
									color="gray.600"
								>
									<Box fontSize="3em">
										<TiCamera />
									</Box>

									<Box>拍照或上傳圖片</Box>
								</Box>
								<StyledInput
									type="file"
									name="merchantImage"
									capture="camera"
									accept="image/*"
									id=""
									onChange={(e) => {
										fileHandler(e.target.files[0])
									}}
									pb="100%"
									border="none"
									opacity="0"
								/>
							</Box>
						</Box>
						<Box>
							<FormLabel d="flex">
								進價<Text color="red">*</Text>
							</FormLabel>
							<StyledInput placeholder="1100101-001" />
						</Box>

						<Box className="noteBox" w="100%">
							<FormLabel d="flex">
								備註<Text color="red">*</Text>
							</FormLabel>
							<Input w="80%" placeholder="1100101-001" />
						</Box>
					</GridBox>
				</Box>
			</StyledBg>
		</>
	)
}
