import React, { useState } from 'react'
import {
	Box,
	Input,
	FormControl,
	FormLabel,
	Spinner,
	Button,
	Stack,
	Image,
} from '@chakra-ui/react'
import styled from 'styled-components'
import { useForm } from 'react-hook-form'
import logoIcon from 'assets/images/logo.svg'
import { login } from 'reducers/auth'
import { useDispatch } from 'react-redux'
import UserService from 'Api/userService'
import loginBg from 'assets/images/loginBg.png'

const StyledLoginPage = styled(Box)`
	width: 100vw;
	min-height: 100vh;
	padding: 2em 0;
	display: flex;
	align-items: center;
	justify-content: center;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
`

const StyledForm = styled.form`
	max-width: 95%;
	text-align: center;
	background-color: white;
	padding: 2em 5% 3em;
	box-shadow: 0px 4px 10px 5px rgba(0, 0, 0, 0.25);
`

const StyledStack = styled(Stack)`
	padding: 1em 0em;
`

const StyledFormControl = styled(FormControl)`
	display: flex;
	align-items: center;
	border-bottom: 1px solid #76a5d1;
	padding: 0 1em;
	color: #76a5d1;
	margin-bottom: 3em !important;
	position: relative;
`

const StyledInput = styled(Input)`
	border: none !important;
	outline: none !important;

	&:focus {
		border: none !important;
		outline: none !important;
		box-shadow: none !important;
	}
`

export default function LoginPage() {
	const {
		register,
		handleSubmit,
		formState: { errors },
	} = useForm()
	const dispatch = useDispatch()
	const [isLoading, setIsLoading] = useState(false)

	const onSubmit = (data) => {
		setIsLoading((pre) => true)
		const userService = new UserService()
		userService.login().then((res) => {
			dispatch(login(res))
			setIsLoading((pre) => false)
		})
	}
	return (
		<StyledLoginPage backgroundImage={`url(${loginBg})`}>
			<StyledForm onSubmit={handleSubmit(onSubmit)}>
				<Image w="80%" maxW="200px" src={logoIcon} d="inline-block" mb="3em" />
				<StyledStack alignItems="center">
					<StyledFormControl id="number">
						<FormLabel whiteSpace="nowrap" my="auto">
							請輸入帳號
						</FormLabel>
						<StyledInput
							type="number"
							placeholder="員工編號"
							{...register('number', { required: true })}
						/>
						<Box w="100%" color="red" left={0} position="absolute" top="100%">
							{errors.number?.type === 'required' && '請輸入你的帳號'}
						</Box>
					</StyledFormControl>

					<StyledFormControl id="password">
						<FormLabel whiteSpace="nowrap" my="auto">
							請輸入密碼
						</FormLabel>
						<StyledInput
							type="password"
							placeholder="密碼"
							{...register('password', { required: true })}
						/>
						<Box w="100%" color="red" left={0} position="absolute" top="100%">
							{errors.password?.type === 'required' && '請輸入你的密碼'}
						</Box>
					</StyledFormControl>

					<Button
						type="submit"
						bgColor="#94BADD"
						color="white"
						_hover={{ bgColor: '#3c75ac' }}
						_active={{ bgColor: '#3c75ac' }}
						padding="0 4.2em"
						height="4.2em"
					>
						{isLoading ? <Spinner /> : '登入'}
					</Button>
				</StyledStack>
			</StyledForm>
		</StyledLoginPage>
	)
}
