import { createSlice } from '@reduxjs/toolkit'
import Cookies from 'js-cookie'
import { useToast } from '@chakra-ui/react'

let tokenCookies = Cookies.get('token')
let nameCookies = Cookies.get('name')

export const authStateSlice = createSlice({
	name: 'auth',
	initialState: {
		isAuth: tokenCookies ? true : false,
		name: nameCookies,
		data: {},
	},
	reducers: {
		login: (state, action) => {
			let status = action.payload.data.status
			let token = action.payload.data.status
			Cookies.set('token', token)
			return { ...state, isAuth: status === 200 ? true : false }
		},
		logout: (state, action) => {
			Cookies.remove('token')

			return { ...state, isAuth: false }
		},
	},
})

export const { login, logout } = authStateSlice.actions
export default authStateSlice.reducer
