import React from 'react'
import { Box, Text } from '@chakra-ui/react'
import styled from 'styled-components'
import { HiPlusSm } from 'react-icons/hi'
import { StyledButtonBox } from 'utils/StyledButton'

const StyledTopbar = styled(Box)`
	display: flex;
	justify-content: space-between;
	background: #f9f9f9;
	padding: 0.5em 1em;
	border-radius: 5px;
	align-items: center;
	margin-bottom: 1em;
	position: relative;
	overflow: hidden;

	&::before {
		content: '';
		position: absolute;
		left: 0%;
		width: 1px;
		height: 100%;
		border-left: 3px solid #b6d3ff;
		border-radius: 20 px;
	}
`

export default function TableTopBar() {
	return (
		<StyledTopbar>
			<Box fontSize="1.25em" fontWeight="700">
				權限設定
			</Box>
			<Box>
				<StyledButtonBox>
					<HiPlusSm />
					<Text ml="5px">新增品牌</Text>
				</StyledButtonBox>
			</Box>
		</StyledTopbar>
	)
}
