import React, { useContext } from 'react'
import { Box, Text, Input, Select } from '@chakra-ui/react'
import styled from 'styled-components'
import { HiPlusSm } from 'react-icons/hi'
import { merchatContext } from './useContext/createContext'
import { FormSubmitButton, StyledButtonBox } from 'utils/StyledButton'
import { MdKeyboardArrowRight } from 'react-icons/md'
import { AiOutlineSearch } from 'react-icons/ai'

const StyledTopbar = styled(Box)`
	display: flex;
	justify-content: space-between;
	background: #f9f9f9;
	padding: 0.5em 1em;
	border-radius: 5px;
	align-items: center;
	margin-bottom: 1em;
	position: relative;
	overflow: hidden;

	&::before {
		content: '';
		position: absolute;
		left: 0%;
		width: 1px;
		height: 100%;
		border-left: 3px solid #b6d3ff;
		border-radius: 20 px;
	}
`

const StyledForm = styled.form`
	display: flex;
	algn-items: center;
`

export default function TableTopBar() {
	const { setSearchValue, setEditState, editState } = useContext(merchatContext)

	return (
		<StyledTopbar>
			<Box fontSize="1.25em" fontWeight="700" d="flex">
				商品管理
				{editState.isEdit ? (
					<Box d="flex" alignItems="center">
						<Box mx=".5em">
							<MdKeyboardArrowRight size="1.5em" />
						</Box>
						{editState.staffNumber ? '編輯員工' + editState.staffNumber : '內容詳情'}
					</Box>
				) : null}
			</Box>
			{editState.isEdit ? null : (
				<StyledForm onSubmit={(e) => console.log(e)}>
					<Select minW="130px" mr="1em">
						<option value="" disabled selected>
							位置
						</option>
						<option value=""></option>
					</Select>
					<Select minW="130px" mr="1em">
						<option value="" disabled selected>
							商品狀態
						</option>
						<option value=""></option>
					</Select>
					<Select minW="130px" mr="1em">
						<option value="" disabled selected>
							商品類型
						</option>
						<option value=""></option>
					</Select>
					<Input
						mr="1em"
						maxW="160px"
						onChange={(e) => {
							setSearchValue(e.target.value)
						}}
						placeholder="請輸入員編或姓名"
					/>
					<FormSubmitButton flexShrink="0" type="submit">
						<AiOutlineSearch />
						<Text ml="5px">搜尋</Text>
					</FormSubmitButton>
				</StyledForm>
			)}
		</StyledTopbar>
	)
}
