import { Box } from '@chakra-ui/react'
import React from 'react'
import {
	useTable,
	usePagination,
	Column,
	useSortBy,
	useFilters,
} from 'react-table'
import { FaPen, FaTrashAlt } from 'react-icons/fa'

export default function TableContent() {
	const data = React.useMemo(
		() => [
			{
				id: '1',
				position: '2',
				description: '123123123123123123123123123',
				management: 'cool',
			},
			{
				id: '1',
				position: '2',
				description: '123123123123123123123123123',
				management: 'cool',
			},
			{
				id: '1',
				position: '2',
				description: '123123123123123123123123123',
				management: 'cool',
			},
			{
				id: '1',
				position: '2',
				description: '123123123123123123123123123',
				management: 'cool',
			},
			{
				id: '1',
				position: '2',
				description: '123123123123123123123123123',
				management: 'cool',
			},
		],
		[]
	)

	const columns = React.useMemo(
		() => [
			{
				Header: '序號',
				accessor: 'id',
				Cell: (row) => {
					return row.value
				},

				disableFilters: true,
			},
			{
				Header: '位置名稱',
				accessor: 'position',
				Cell: (row) => {
					return row.value
				},
			},
			{
				Header: '說明',
				accessor: 'description',
				disableFilters: true,
			},
			{
				Header: '管理',
				accessor: 'management',
				disableFilters: true,
				Cell: (row) => {
					return (
						<Box d="flex" justifyContent="space-around">
							<Box color="#91B0EB" cursor="pointer">
								<FaPen />
							</Box>
							<Box color="#EB9191" cursor="pointer">
								<FaTrashAlt />
							</Box>
						</Box>
					)
				},
			},
		],
		[]
	)

	const {
		getTableProps,
		getTableBodyProps,
		headerGroups,
		page,
		prepareRow,

		// pagination
		// canPreviousPage,
		// canNextPage,
		// pageOptions,
		// pageCount,
		// gotoPage,
		// nextPage,
		// previousPage,
		// state: { pageIndex },
	} = useTable(
		{
			columns,
			data,

			initialState: { pageIndex: 0, pageSize: 25 },
		},
		useFilters,
		useSortBy,
		usePagination
	)
	return (
		<>
			<table {...getTableProps()}>
				<thead>
					{headerGroups.map((headerGroup, g) => (
						<tr {...headerGroup.getHeaderGroupProps()} key={g}>
							{headerGroup.headers.map((column, k) => {
								return (
									<th key={k}>
										<Box
											{...column.getHeaderProps(column.getSortByToggleProps())}
										>
											{
												// Render the header
												column.render('Header')
											}
										</Box>
									</th>
								)
							})}
						</tr>
					))}
				</thead>
				<tbody {...getTableBodyProps()}>
					{
						// Loop over the table rows
						page.map((row, i) => {
							// Prepare the row for display
							prepareRow(row)
							return (
								// Apply the row props
								<tr {...row.getRowProps()} key={i}>
									{
										// Loop over the rows cells
										row.cells.map((cell, j) => {
											// console.log(cell.column.Header)
											// Apply the cell props
											return (
												<td {...cell.getCellProps()} key={j}>
													{
														// Render the cell contents
														cell.render('Cell')
													}
												</td>
											)
										})
									}
								</tr>
							)
						})
					}
				</tbody>
			</table>
		</>
	)
}
