import {
	Accordion,
	AccordionItem,
	AccordionButton,
	AccordionPanel,
	AccordionIcon,
	Box,
	Checkbox,
	Button,
	Input,
	Select,
	Textarea,
} from '@chakra-ui/react'
import { permissionList } from './permissionList'
import styled, { keyframes } from 'styled-components'
import { useForm } from 'react-hook-form'
import { staffContext } from './useContext/createContext'
import { useContext } from 'react'

const fadeInRight = keyframes`
  0% {
    transform:translate(100%)
  }
  100% {
	transform:translate(0%)
  }
`

const StyledBg = styled(Box)`
	width: 100%;
	display: flex;
	padding: 2em 2em 0;
	border-radius: 4px;
	background-color: #f9f9f9;
	flex-wrap: wrap;
	justify-content: space-between;
`

const StyledForm = styled.form`
	width: 100%;
	animation: ${fadeInRight} 0.5s ease;

	input[type='checkbox'] {
		border: 1px solid;
		padding: 5px;
		appearance: none;
		outline: none;
	}

	input[type='checkbox']:checked {
		appearance: auto;
		background: none !important;
		color: black !important;
	}
`

//TODO: 把所有選項都變成component

export default function SubContent({ setEditStatus }) {
	const { setEditState } = useContext(staffContext)
	const { register, handleSubmit } = useForm()
	return (
		<StyledForm onSubmit={handleSubmit((e) => console.log(e))}>
			<Box d="flex" flexWrap="wrap" justifyContent="space-between" mb="1.5em">
				<Box>
					<Box d="flex">
						員工編號 <Box color="red">*</Box>
					</Box>
					<Input {...register('staffNumber', { required: true })} />
				</Box>
				<Box>
					<Box d="flex">
						員工姓名<Box color="red">*</Box>
					</Box>
					<Input {...register('staffName', { required: true })} />
				</Box>
				<Box>
					<Box d="flex">
						員工電話 <Box color="red">*</Box>
					</Box>
					<Input {...register('staffPhone', { required: true })} />
				</Box>
				<Box>
					<Box d="flex">
						權限等級 <Box color="red">*</Box>
					</Box>
					<Select minW="180px" {...register('staffLevel', { required: true })}>
						<option value="員工">員工</option>
						<option value="老闆">老闆</option>
						<option value="老闆娘">老闆娘</option>
					</Select>
				</Box>
			</Box>
			<StyledBg overflow="auto">
				{permissionList.map((items, t) => (
					<Box w="25%" minW="290px" mb="1em" key={t}>
						<Box fontSize="1.43em" color="#8F8F8F">
							{items.name}
						</Box>
						<Accordion allowToggle>
							{items.permission_type.map((item, j) => (
								<AccordionItem
									border="none"
									d="flex"
									alignItems="center"
									flexWrap="wrap"
									key={j}
								>
									<input type="checkbox" />
									<AccordionButton
										flexShrink="0"
										w="100px"
										p="0.5em 0"
										_active={{ outline: 'none', boxShadow: 'none' }}
										_focus={{ outline: 'none', boxShadow: 'none' }}
									>
										<Box ml=".5em" textAlign="left" isTruncated>
											{item.name}
										</Box>
										<AccordionIcon />
									</AccordionButton>

									<AccordionPanel pb={4} w="100%" d="flex" flexWrap="wrap">
										{item.permission.map((el, i) => (
											<Box d="flex" alignItems="center" mr="1em" key={i}>
												<input
													type="checkbox"
													id="vehicle1"
													name="vehicle1"
													{...register(el.name)}
												/>
												<Box ml=".5em">{el.name}</Box>
											</Box>
										))}
									</AccordionPanel>
								</AccordionItem>
							))}
						</Accordion>
					</Box>
				))}
			</StyledBg>
			<Box>
				<Box my={['.5em', '1em']}>備註</Box>
				<Textarea {...register('remark')} />
			</Box>
			<Box textAlign="center" py="1em">
				<Button
					mx=".5em"
					onClick={() =>
						setEditState((pre) => (pre = { isEdit: false, staffNumber: '' }))
					}
				>
					取消
				</Button>
				<Button mx=".5em" type="submit">
					儲存
				</Button>
			</Box>
		</StyledForm>
	)
}
