import {
	Box,
	Input,
	InputGroup,
	InputRightElement,
	FormLabel,
	Text,
} from '@chakra-ui/react'

import styled, { keyframes } from 'styled-components'
import { useForm } from 'react-hook-form'
import { purchaseContext } from './useContext/createContext'
import { useContext, useState } from 'react'
import { AiOutlineSearch, AiOutlinePlus } from 'react-icons/ai'
import { FormSubmitButton } from 'utils/StyledButton'
import { FaPlus } from 'react-icons/fa'
import SingleItem from './component/SingleItem'
import { useEffect } from 'react'
import axios from 'axios'

const fadeInRight = keyframes`
  0% {
    transform:translate(100%)
  }
  100% {
	transform:translate(0%)
  }
`

const StyledTransitionBg = styled(Box)`
	width: 100%;
	animation: ${fadeInRight} 0.5s ease;
`

const StyledForm = styled.form`
	width: 100%;
`

const merchantItemData = {
	merchantID: '',
	merchantName: '',
}

export default function SubContent({ setEditStatus }) {
	const { setEditState } = useContext(purchaseContext)
	const { register, handleSubmit } = useForm()
	const [merchantListData, setMerchantListData] = useState([merchantItemData])

	const newMerchant = () => {
		setMerchantListData((pre) => pre.concat(merchantItemData))
	}

	const submitHandler = (e) => {
		setEditState((pre) => (pre = { ...pre, step: 2 }))
	}
	return (
		<StyledTransitionBg>
			<Box d="flex" flexWrap="wrap" mb="1.5em" alignItems="center">
				{/* TODO: 這個FORM要另外拉出去做一個表單，不然在這裡用同一個useform()，值都會被submit，即便位置不在另一個form內 */}
				<Box d="flex">
					<InputGroup mr=".5em">
						<Input placeholder="請輸入廠商電話" />
						<InputRightElement children={<AiOutlineSearch />} />
					</InputGroup>
					<Box
						borderRadius="4px"
						fontSize="1.5em"
						d="flex"
						justifyContent="center"
						alignItems="center"
						bgColor="#C4C4C4"
						color="white"
						cursor="pointer"
						flexShrink="0"
						w="35px"
						_hover={{ bgColor: '#737373' }}
					>
						<AiOutlinePlus />
					</Box>
				</Box>
				<Box d="flex" borderBottom="1px solid" px=".7em" py=".3em" ml="3em">
					<Box>王大明</Box>
					<Box ml="1em">0912345678</Box>
				</Box>
			</Box>

			{/* TODO: 這個FORM的值再用物件的FOR LOOP塞進ARRAY出去 */}
			<StyledForm
				onSubmit={handleSubmit((e) => submitHandler(merchantListData))}
			>
				{merchantListData.map((item, i) => {
					return (
						<SingleItem
							register={register}
							order={i}
							key={i}
							setMerchantListData={setMerchantListData}
							merchantListData={merchantListData}
						/>
					)
				})}
				<Box
					bgColor="gray.100"
					p="1em 2em"
					borderRadius="10px"
					my="1em"
					onClick={newMerchant}
					cursor="pointer"
					_hover={{ bgColor: 'gray.600', color: 'white' }}
					transition="all .2s ease"
				>
					<Box textAlign="center">
						<Box d="inline-block" mb="1em">
							<FaPlus size="30px" />
						</Box>
						<Box>添加下一筆</Box>
					</Box>
				</Box>
				<Box textAlign="center" py="1em">
					<FormSubmitButton
						mx=".5em"
						onClick={() =>
							setEditState((pre) => (pre = { step: 0, stepWording: '' }))
						}
					>
						取消
					</FormSubmitButton>
					<FormSubmitButton mx=".5em" type="submit">
						下一步
					</FormSubmitButton>
				</Box>
			</StyledForm>
		</StyledTransitionBg>
	)
}
