import React from 'react'
import { Box, Modal, ModalOverlay, ModalContent, ModalHeader, ModalFooter, ModalBody, ModalCloseButton, Button, useDisclosure, FormLabel, Input, Text } from '@chakra-ui/react'
import { FaPen, FaTrashAlt, FaTags, FaCaretSquareDown } from 'react-icons/fa'
import { RiMoneyDollarBoxFill } from 'react-icons/ri'
import { FormSubmitButton, StyledButtonBox } from 'utils/StyledButton'
import styled from 'styled-components'

const StyledGridTable = styled(Box)`
	display: grid;
	grid-template-columns: 1fr 1fr 2fr;
	column-gap: 0.5em;
	row-gap: 0.5em;
`

export default function ReusedModal({ type }) {
	const { isOpen, onClose, onOpen } = useDisclosure()

	function modalIconSwitcher(type) {
		switch (type) {
			case '定價':
				return <RiMoneyDollarBoxFill />
			case '標籤':
				return <FaTags />
			case '保留':
				return <FaCaretSquareDown />
			case '刪除':
				return <FaTrashAlt color="#EB9191" />
			default:
				return
		}
	}

	function modalTitleSwitcher(type) {
		switch (type) {
			case '定價':
				return '售價新增/修改'
			case '標籤':
				return '不知道這是做啥的'
			case '保留':
				return '是否確定保留?'
			case '刪除':
				return '是否確定刪除?'
			default:
				return
		}
	}

	return (
		<>
			<Box color="#91B0EB" cursor="pointer" onClick={onOpen}>
				{modalIconSwitcher(type)}
			</Box>
			<Modal isOpen={isOpen} onClose={onClose} size="xl">
				<ModalOverlay />
				<ModalContent>
					<ModalHeader textAlign="center" fontSize="2em" my=".5em">
						{modalTitleSwitcher(type)}
					</ModalHeader>
					<ModalCloseButton />
					<ModalBody textAlign="center">
						{type === '定價' ? (
							<>
								<StyledGridTable>
									<Box>時間</Box>
									<Box>售價</Box>
									<Box>說明</Box>
									<Input />
									<Input />
									<Input />
									<Input />
									<Input />
									<Input />
									<Input />
									<Input />
									<Input />
								</StyledGridTable>
							</>
						) : null}
						{type === '標籤' ? <ModalBody>4654564564564</ModalBody> : null}
						{type === '保留' ? (
							<>
								<Box fontSize="1.3em" color="gray.700">
									<Text mb=".5em">11000101-002</Text>
									<Text mb=".5em">韓式短版上衣</Text>
								</Box>
								<Box maxW="200px" mx="auto">
									<FormLabel>保留說明</FormLabel>
									<Input />
								</Box>
							</>
						) : null}
						{type === '刪除' ? (
							<>
								<Box fontSize="1.3em" color="gray.700">
									<Text mb=".5em">11000101-002</Text>
									<Text mb=".5em">韓式短版上衣</Text>
								</Box>
							</>
						) : null}
					</ModalBody>
					<ModalFooter justifyContent="center" mb="2em" mt="3em">
						<StyledButtonBox onClick={onClose} mr="1em">
							取消
						</StyledButtonBox>
						<FormSubmitButton>確定</FormSubmitButton>
					</ModalFooter>
				</ModalContent>
			</Modal>
		</>
	)
}
