import { Box } from '@chakra-ui/react'
import React, { useContext, useEffect } from 'react'
import {
	useTable,
	usePagination,
	useSortBy,
	useGlobalFilter,
} from 'react-table'
import { staffContext } from './useContext/createContext'
import { FaPen, FaTrashAlt } from 'react-icons/fa'
import { StyledPaginationButton as StyledButton } from 'utils/StyledButton'

export default function TableContent() {
	const { searchValue, setEditState } = useContext(staffContext)

	useEffect(() => {
		setGlobalFilter(searchValue)
	}, [searchValue])
	const data = React.useMemo(
		() => [
			{
				id: 'OPMM001',
				name: 'HENRY',
				phone: '0900123456',
				level: 'CEO',
				remark: '一天只能工作一小時，不能再多',
			},
			{
				id: 'OPMM002',
				name: 'JESUS',
				phone: '0900123456',
				level: 'CTO',
				remark: 'IQ160，EQ16',
			},
			{
				id: 'OPMM003',
				name: 'DAVID',
				phone: '0900123456',
				level: 'CCC',
				remark: 'IQ16，EQ160',
			},
			{
				id: 'OPMM004',
				name: 'DOLPHIN',
				phone: '0900123456',
				level: 'COO',
				remark: '本周加班已超過80小時',
			},
			{
				id: 'OPMM001',
				name: 'HENRY',
				phone: '0900123456',
				level: 'CEO',
				remark: '一天只能工作一小時，不能再多',
			},
			{
				id: 'OPMM002',
				name: 'JESUS',
				phone: '0900123456',
				level: 'CTO',
				remark: 'IQ160，EQ16',
			},
			{
				id: 'OPMM003',
				name: 'DAVID',
				phone: '0900123456',
				level: 'CCC',
				remark: 'IQ16，EQ160',
			},
			{
				id: 'OPMM004',
				name: 'DOLPHIN',
				phone: '0900123456',
				level: 'COO',
				remark: '本周加班已超過80小時',
			},
			{
				id: 'OPMM001',
				name: 'HENRY',
				phone: '0900123456',
				level: 'CEO',
				remark: '一天只能工作一小時，不能再多',
			},
			{
				id: 'OPMM002',
				name: 'JESUS',
				phone: '0900123456',
				level: 'CTO',
				remark: 'IQ160，EQ16',
			},
			{
				id: 'OPMM003',
				name: 'DAVID',
				phone: '0900123456',
				level: 'CCC',
				remark: 'IQ16，EQ160',
			},
			{
				id: 'OPMM004',
				name: 'DOLPHIN',
				phone: '0900123456',
				level: 'COO',
				remark: '本周加班已超過80小時',
			},
			{
				id: 'OPMM001',
				name: 'HENRY',
				phone: '0900123456',
				level: 'CEO',
				remark: '一天只能工作一小時，不能再多',
			},
			{
				id: 'OPMM002',
				name: 'JESUS',
				phone: '0900123456',
				level: 'CTO',
				remark: 'IQ160，EQ16',
			},
			{
				id: 'OPMM003',
				name: 'DAVID',
				phone: '0900123456',
				level: 'CCC',
				remark: 'IQ16，EQ160',
			},
			{
				id: 'OPMM004',
				name: 'DOLPHIN',
				phone: '0900123456',
				level: 'COO',
				remark: '本周加班已超過80小時',
			},
		],
		[]
	)

	const columns = React.useMemo(
		() => [
			{
				Header: '員工編號',
				accessor: 'id',
				Cell: (row) => {
					return row.value
				},
			},
			{
				Header: '姓名',
				accessor: 'name',
				Cell: (row) => {
					return row.value
				},
			},
			{
				Header: '電話',
				accessor: 'phone',
				disableGlobalFilter: true,
			},

			{
				Header: '權限等級',
				accessor: 'level',
				disableGlobalFilter: true,
			},
			{
				Header: '備註',
				accessor: 'remark',
				disableGlobalFilter: true,
			},
			{
				Header: '管理',
				accessor: 'management',
				disableGlobalFilter: true,
				Cell: (row) => {
					return (
						<Box d="flex" justifyContent="space-around">
							<Box
								color="#91B0EB"
								cursor="pointer"
								onClick={() =>
									setEditState(
										(pre) =>
											(pre = { isEdit: true, staffNumber: row.row.original.id })
									)
								}
							>
								<FaPen />
							</Box>
							<Box color="#EB9191" cursor="pointer">
								<FaTrashAlt />
							</Box>
						</Box>
					)
				},
			},
		],
		[]
	)

	const {
		getTableProps,
		getTableBodyProps,
		headerGroups,
		page,
		prepareRow,
		setGlobalFilter,
		pagination,
		canPreviousPage,
		canNextPage,
		pageOptions,
		pageCount,
		gotoPage,
		nextPage,
		previousPage,
		state: { pageIndex },
	} = useTable(
		{
			columns,
			data,
			initialState: { pageIndex: 0, pageSize: 10 },
		},
		useGlobalFilter,
		useSortBy,
		usePagination
	)
	return (
		<>
			<table {...getTableProps()}>
				<thead>
					{headerGroups.map((headerGroup, g) => (
						<tr {...headerGroup.getHeaderGroupProps()} key={g}>
							{headerGroup.headers.map((column, k) => (
								<th key={k}>
									<Box
										{...column.getHeaderProps(column.getSortByToggleProps())}
									>
										{
											// Render the header
											column.render('Header')
										}
									</Box>
								</th>
							))}
						</tr>
					))}
				</thead>
				<tbody {...getTableBodyProps()}>
					{
						// Loop over the table rows
						page.map((row, i) => {
							// Prepare the row for display
							prepareRow(row)
							return (
								// Apply the row props
								<tr {...row.getRowProps()} key={i}>
									{
										// Loop over the rows cells
										row.cells.map((cell, j) => {
											// console.log(cell.column.Header)
											// Apply the cell props
											return (
												<td {...cell.getCellProps()} key={j}>
													{
														// Render the cell contents
														cell.render('Cell')
													}
												</td>
											)
										})
									}
								</tr>
							)
						})
					}
				</tbody>
			</table>
			<Box d="flex" justifyContent="center" alignItems="center" pt="1em">
				<StyledButton onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
					{'<<'}
				</StyledButton>
				<StyledButton
					onClick={() => previousPage()}
					disabled={!canPreviousPage}
				>
					{'<'}
				</StyledButton>
				<Box color="#2250a6" fontSize="1.3em" mx=".5em">
					{pageIndex + 1}
				</Box>
				<StyledButton onClick={() => nextPage()} disabled={!canNextPage}>
					{'>'}
				</StyledButton>
				<StyledButton
					onClick={() => gotoPage(pageCount - 1)}
					disabled={!canNextPage}
				>
					{'>>'}
				</StyledButton>
			</Box>
		</>
	)
}
