import { Box, Flex, Text } from '@chakra-ui/layout'
import styled from 'styled-components'
import { useDispatch } from 'react-redux'
import { toggle, setRoute } from 'reducers/sidebar'
import {
	IoIosArrowForward,
	IoIosArrowDown,
	IoIosCheckboxOutline,
} from 'react-icons/io'
import { BsPerson } from 'react-icons/bs'
import { FiBox, FiTag } from 'react-icons/fi'
import {
	IoHomeOutline,
	IoDocumentTextOutline,
	IoShirtOutline,
} from 'react-icons/io5'
import { AiOutlineAppstore } from 'react-icons/ai'
import { HiReceiptRefund } from 'react-icons/hi'

const StyledItem = styled(Box)`
	margin: 0 auto 0.5em;
	background-color: transparent;
	display: flex;
	flex-direction: column;
	align-items: center;
	color: white;
	overflow-y: hidden;
	transition: all 0.3s ease;
`

export default function SideBarItem({ title, children, isToggle }) {
	const dispatch = useDispatch()

	const iconSwitcher = (title) => {
		switch (title) {
			case '人事管理':
				return <BsPerson />
			case '進貨管理':
				return <FiBox />
			case '庫存管理':
				return <IoShirtOutline />
			case '廠商管理':
				return <IoHomeOutline />
			case '銷貨管理':
				return <IoIosCheckboxOutline />
			case '付款管理':
				return <FiTag />
			case '報表管理':
				return <IoDocumentTextOutline />
			case '退貨管理':
				return <HiReceiptRefund />
			case '其他管理':
				return <AiOutlineAppstore />
			default:
				return
		}
	}

	return (
		<StyledItem>
			<Flex
				alignItems="center"
				cursor="pointer"
				my="1em"
				onClick={() => {
					dispatch(toggle(title))
				}}
				opacity={isToggle ? '1' : '.5'}
				_hover={{
					opacity: '1',
				}}
				pl=".5em"
			>
				<Box min-width="40px" flexShrink={0} fontSize="1.5em">
					{iconSwitcher(title)}
				</Box>

				<Text mr="1.5em" ml="1em" whiteSpace="nowrap">
					{title}
				</Text>
				{!isToggle ? <IoIosArrowForward /> : <IoIosArrowDown />}
			</Flex>
			{children ? (
				<Box
					borderRadius="12px"
					bg="white"
					color="#A7A7A7"
					maxH={isToggle ? '100%' : '0'}
					transition="all .1s linear"
				>
					{children.map((item, i) => (
						<Box
							ml="3em"
							mr="2.5em"
							my="1em"
							cursor="pointer"
							_hover={{ color: '#2B64D0' }}
							key={i}
							onClick={() => dispatch(setRoute(item.title))}
						>
							{item.title}
						</Box>
					))}
				</Box>
			) : null}
		</StyledItem>
	)
}
